
@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;400;500;600;700;800;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap");
* {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  box-sizing: border-box;
  user-select: none;
  -webkit-user-drag: none;
  outline: none;
  stroke: none;
  scroll-behavior: smooth;
}
::-webkit-scrollbar,
::-webkit-scrollbar-thumb,
::-webkit-scrollbar-track {
  display: none;
  width: 0;
}
.App {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: black;
  overflow: hidden;
}
body {
  background-color: black;
}
#root {
  width: 100%;
  background-color: black;
  zoom: 0.8;
}

.reveal {
  transform: translateY(90px);
  opacity: 0;
  position: relative;
  transition: all 0.85s ease-in;
}

.reveal.active {
  transform: translate(0px);
  opacity: 1;
}
.reveal.overlay.active {
  margin-top: 0%;
  padding-top: 2%;
}
ul.navbar.show {
  position: absolute;
  flex-direction: column;
  align-items: flex-end;
  background: orange;
  display: flex;
  color: black;
  row-gap: -1rem;
  transform: translate(-39.4%, 60%);
  padding: 5vh 3vw 30vh 58vw;
  border-radius: 2rem 2rem 2rem 2rem;
  row-gap: 1rem;
}
.window h1 {
  font-size: 500%;
  font-family: "Montserrat", sans-serif;
  color: #ff8400;
}
.idk {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  padding: 1em 25%;
  backdrop-filter: blur(3px) brightness(0.85);
}
@media only screen and (max-width: 1024px) {
  .idk {
    padding: 0 35%;
    width: 100%;
  }
  .content {
    padding: 0 47em;
  }
  p.indexTitle.mainSection {
    font-size: 75%;
  }

  h1.mainSection.secondHeaderIg {
    font-size: 130%;
  }
  h1.downlaodHeading {
    font-size: 40px;
    font-weight: 600;
    margin-bottom: 5%;
  }
  .aboutleft {
    flex-direction: column;
    display: flex;
    align-items: center;
    position: relative;
    font-size: 22px;
    margin: 0;
  }
  p.textDescription.glow-text {
    margin: 1em 0px 4em;
    font-size: 15px;
  }
  .reveal.aboutleft.active {
    margin: 0;
  }
  .textInfo {
    font-size: 30px;
  }
  div#about {
    padding: 0 1%;
    width: 100%;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  h1.aboutheader.reveal.mainSection.active {
    white-space: nowrap;
  }
  button.read-more-btn.mainSection {
    font-size: 20px;
  }

  .aboutright img {
    width: 80%;
    padding: 2em;
  }
  video {
    left: 1rem;
    padding: 1px;
    margin: 0;
    width: 130rem;
  }
  .c2 {
    white-space: nowrap;
  }
  .window img {
    display: none;
  }
  .window {
    text-align: center;
  }
  .window h1 {
    font-size: 4px;
  }
  .user.pc,
  .fasttt,
  .lugo.logo {
    display: none;
  }
  header {
    justify-content: center;
  }
  svg#menu {
    padding: 0.75rem;
  }
  ul.sub-menu {
    transform: translate(-19%, 19%);
  }
  .menu-item:hover > ul {
    position: absolute;
    width: 175%;
  }
  ul.sub-menu.hmm {
    transform: translate(-56%, 72%);
  }
  ul.navbar.show {
    position: absolute;
    flex-direction: column;
    background: orange;
    display: flex;
    color: black;
    padding: 2em;
    border-radius: 2rem 2rem 2rem 2rem;
    row-gap: 0.75rem;
    left: 24%;
    transform: translate(48%, 0%);
    width: 36rem;
    height: 70vh;
    text-align: center;
    align-items: center;
  }
}
@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .aboutright img {
    width: 150%;
  }
  .content {
    padding: 0 47em;
  }
}
@media only screen and (max-width: 480px) {
  h1.text-shadow {
    font-size: 90%;
  }
  .content {
    padding: 0 47em;
  }
  .idk {
    padding: 0 20%;
  }
  video {
    left: 1rem;
    margin: 0;
    padding: 1px;
    width: 75rem;
  }
  .reveal.aboutleft.active {
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 0;
    font-size: 20px;
  }

  button.read-more-btn.mainSection {
    font-size: 115%;
  }

  .c2 {
    white-space: nowrap;
  }
  .window img {
    display: none;
  }
  .window {
    text-align: center;
  }
  .window h1 {
    font-size: 4px;
  }
  ul.navbar.show {
    transform: translate(10%, 0%);
    width: 34rem;
    height: 80vh;
  }
  .user.pc,
  .fasttt,
  .lugo.logo {
    display: none;
  }
  header {
    column-gap: 5%;
    justify-content: center;
  }
  svg#menu {
    padding: 0.75rem;
  }
  .menu-item:hover > ul {
    position: absolute;
    width: 175%;
  }
  ul.sub-menu {
    transform: translate(-11%, 19%);
  }
  ul.sub-menu.hmm {
    transform: translate(-58%, 72%);
  }
  .hmm li a {
    white-space: normal;
    width: 30rem;
  }
}
@media only screen and (min-width: 480px) and (max-width: 768px) {
  .reveal.aboutleft.active {
    padding: 0 12%;
  }
  .window h1 {
    font-size: 275%;
  }
  .content {
    padding: 0 47em;
  }
  .idk {
    padding: 0 36%;
  }
  

}


