header {
  flex-direction: row;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100px;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  text-decoration: none;
  list-style: none;
  color-scheme: dark;
  scroll-behavior: smooth;
}

:root {
  --bg-color: #222327;
  --text-color: #000000;
  --white: #fff;
  --main-color: #f9a602;
  --skky: #00f2ff;
  --pattern-color: #f5e90a64;
  --a-color: #141517;
  --aa-color: #222327;
}

body {
  min-height: 100vh;
  background: var(--bg-color);
  color: var(--text-color);

  background-color: #0f0f0f;
  background-image: url("https://media.discordapp.net/attachments/1176230899214450780/1176231231218782208/banner_lines_1.png?ex=656e1db2&is=655ba8b2&hm=6547b84524b073151f32e0139ad548397144f21508e372c4cc6a647fa00b19c8&=&width=861&height=610");
  background-size: 150%;
  /* background-repeat: repeat-x; */
}

header {
  position: relative;
  width: 100%;
  top: 0;
  right: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 28px 12%;
  transition: all 0.5s ease;
}
.navright {
  display: flex;
  align-items: center;
  gap: 20px;
}
.logo {
  display: flex;
  align-items: center;
}
.logo span {
  color: var(--white);
  font-size: 1.7rem;
  font-weight: 600;
  line-height: 0.9;
}

.firmtext {
  color: #f9a602;
}
.firmtext:hover {
  color: #fff;
}
.navbar {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-around;
}
.navleft {
  display: flex;
  align-items: center;
}
.copyright {
  color: #fff;
  margin: 1em 2em;
  text-align: center;
  margin-bottom: 40px;
}
.navbar a {
  color: var(--white);
  font-size: 1.1rem;
  font-weight: 500;
  padding: 5px 0;
  margin: 0px 15px;
  transition: all 0.5s ease;
}
.navbar a:hover {
  /* color: var(--main-color); */
  color: white;
}
.navbar a.active {
  /* color: var(--main-color); */
}
.main {
  display: flex;
  align-items: center;
}
.user {
  margin-right: 25px;
  margin-left: 10px;
  color: var(--bg-color);
  font-size: 1.1rem;
  font-weight: 500;
  transition: all 0.5s ease;
  background-color: var(--white);
  box-sizing: border-box;
  border-radius: 100px;
  padding: 10px;
  margin: 0 2px;
}

.user {
  display: flex;
  align-items: center;
}
.user i {
  color: var(--bg-color);
  font-size: 28px;
  margin-right: 7px;
}
.user:hover {
  background-color: var(--main-color);
  box-shadow: 0 0 8px var(--main-color);
  transition: all 0.5s ease;
}
#menu-icon {
  font-size: 35px;
  color: var(--white);
  cursor: pointer;
  z-index: 10001;
  display: none;
}

/* Add CSS for the reveal animation */
.reveal-text {
  font-size: 24px;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.5s, transform 0.5s;
}

.dropdown {
  color: black;
  font-size: 1.1rem;
  font-weight: 500;
  transition: all 0.5s ease;
  /* background-color: var(--main-color); */
  box-sizing: border-box;
  border-radius: 100px;
  padding: 10px;
  margin: 0 2px;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: var(--main-color);
  min-width: 160px;
  right: 30%;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 5px 0;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
  max-height: 100px;
  overflow-y: auto;
}

.dropdown-content a {
  display: block;
  color: var(--text-color);
  padding: 10px 20px;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
  color: black;
}

.dropdown a {
  color: black;
}

.dropdown-content a:hover {
  color: white;
  transition: all 0.3s ease-in-out;
}
.navbar .dropdown .dropdown-content {
  display: block;
  max-height: 0px;
  overflow: hidden;
  background-color: transparent;
  box-shadow: none;
}

.navbar .dropdown:hover .dropdown-content {
  display: block;
  min-height: 150px;
  transition: all 0.5s ease;
  background-color: #f9a602;
}

.dropdownWrap {
  max-height: 150px;
  overflow-y: scroll;
}

.dropdownWrap::-webkit-scrollbar {
  width: 0.5em;
}

.dropdownWrap::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px var(--main-color);
}

.dropdownWrap::-webkit-scrollbar-thumb {
  background-color: var(--bg-color);
  outline: 1px solid var(--bg-color);
}

.navbar .dropdown:hover a#faq-link > box-icon {
  animation: arrow-down 0.5s forwards;
  transition: all 0.5s ease-in-out;
}

@keyframes arrow-down {
  from {
    transform: rotate(180deg);
  }
  to {
    transform: rotate(0deg);
  }
}

@media (max-width: 1000px) {
  .navbar {
    position: absolute;
    top: 100%;
    right: -100%;
    width: 270px;
    height: 32vh;
    background: var(--main-color);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    border-radius: 15px;
    transition: all 0.5s ease;
    box-shadow: 0 0 8px var(--main-color);
  }

  .navbar.open {
    right: 2%;
  }

  .dropdown-content {
    display: block;
    position: absolute;
    background-color: #fec34b;
    /* min-width: 160px; */
    width: 100%;
    height: 500px;
    overflow-y: scroll;
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    padding: 5px 0;
    border-radius: 5px;
    transition: all 0.3s ease-in-out;
    max-height: 150px;
  }

  .navbar .dropdown:hover a#faq-link > box-icon {
    animation: arrow-down 0.5s forwards;
    transition: all 0.5s ease-in-out;
  }

  .dropdown-content:hover {
    display: block !important;
    max-height: 550px;
    transition: all 0.5s ease;
    background-color: #f9a602;
  }
}

.reveal-text {
  opacity: 0;
  transform: translateY(20px); /* Initial position below */
  animation: reveal 1s ease forwards; /* Adjust animation duration as needed */
}

@keyframes reveal {
  0% {
    opacity: 0;
    transform: translateY(20px); /* Start position */
  }
  100% {
    opacity: 1;
    transform: translateY(0); /* End position (visible) */
  }
}
