#zipModal {
  display: block;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.4);
}

#zipModalContent {
  /* overflow: hidden; */
  background-color: #f7ae1b;
  margin: 50vh auto;
  padding: 33px;
  border: 1px solid #aba9a9;
  width: 50%;
  border-radius: 10px;
  text-align: center;
  transition: all 0.8s ease;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.19), 0px 6px 6px rgba(0, 0, 0, 0.23);
}

#loadingSpinner {
  display: none;
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-top: 4px solid #3498db;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  animation: spin 1s ease infinite;
  /* margin: 3px ; */
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

#errorText {
  color: red;
  margin-top: 10px;
}

#currentLocation {
  text-align: center;
}

#zipModalContent h1 {
  font-size: 20px;
  font-family: "montserrat", sans-serif;
  margin-bottom: 30px;
  transition: all 0.5s ease;
}

#zip {
  padding: 10px;
  width: 100%;
  /* max-width: 200px; */
  margin-bottom: 10px;
  border: none;
  border-radius: 5px;
  box-shadow: inset 0 -2px #ccc;
}

#submitBtn {
  background-color: #fff;
  color: #000000;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.5s ease;
}

#submitBtn:hover {
  background-color: #ccc;
}

@media (max-width: 768px) {
  #zipModalContent {
    width: 50%;
    margin: 20% auto;
  }

  #zipModalContent h1 {
    font-size: 16px;
  }

  #submitBtn {
    padding: 8px 16px;
  }
}

@media (max-width: 768px) {
  #zipModalContent {
    width: 80%; /* Adjusted width for better responsiveness on smaller screens */
    margin: 40% auto;
  }
}

