.gallery {
  --s: 130px;
  --g: 10px;
  display: grid;
  margin: calc(var(--s) + var(--g));
}

.gallery > img {
  grid-area: 1/1;
  width: var(--s);
  aspect-ratio: 0.866;
  object-fit: cover;
  clip-path: polygon(0% 25%, 0% 75%, 50% 100%, 100% 75%, 100% 25%, 50% 0);
  transform: translate(var(--_x, 0), var(--_y, 0)) scale(var(--_t, 1));
  cursor: pointer;
  filter: grayscale(80%);
  transition: 0.2s linear;
}

.gallery img {
  background-color: white;
}

.gallery > img:hover {
  filter: grayscale(0);
  z-index: 1;
  --_t: 1.2;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.573);
}

.gallery > img:nth-child(1) {
  --_x: calc(-100% - var(--g));
}

.gallery > img:nth-child(7) {
  --_x: calc(100% + var(--g));
}

.gallery > img:nth-child(3),
.gallery > img:nth-child(5) {
  --_y: calc(-75% - 0.87 * var(--g));
}

.gallery > img:nth-child(4),
.gallery > img:nth-child(6) {
  --_y: calc(75% + 0.87 * var(--g));
}

.gallery > img:nth-child(3),
.gallery > img:nth-child(4) {
  --_x: calc(-50% - 0.5 * var(--g));
}

.gallery > img:nth-child(5),
.gallery > img:nth-child(6) {
  --_x: calc(50% + 0.5 * var(--g));
}

.app-container {
  position: relative;
  overflow: hidden;
  height: 100vh;
  animation: animate 4s linear infinite;
}

@keyframes animate {
  0% {
    filter: hue-rotate(0deg);
  }
  100% {
    filter: hue-rotate(360deg);
  }
}

.row {
  display: inline-flex;
  margin-left: -50px;
  margin-top: -32px;
  overflow: hidden;
}

.row:nth-child(even) {
  margin-left: 1px;
}

.hexagon {
  position: relative;
  height: 110px;
  width: 100px;
  background: #111;
  margin: 1px;
  clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
  transition: 2s;
}

.hexagon:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  pointer-events: none;
  background: rgba(255, 255, 255, 0.02);
}

.hexagon:hover {
  transition: 0s;
  background: #0f0;
  cursor: alias;
}
