    display: none; /* Initially hide testimonials */
  }

  .testimonial-container {
    flex-direction: column;
    align-items: center;
    overflow: hidden;
  }
  .testimonial {
    width: 100%;
  }
  .testimonial-dots {
    text-align: center;
  }
  .dot {
    display: inline-block;
    width: 10px;
    height: 10px;
    background-color: transparent; /* Make the background transparent */
    border: 1px solid var(--white); /* Add a white border */
    border-radius: 50%;
    margin: 0 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  .dot.active {
    background-color: var(
      --white
    ); /* Set background color to white for the selected dot */
    border: 1px solid transparent; /* Remove the border for the selected dot */
  }
