.carousel-container {
    overflow: hidden;
    position: relative;
    height: 100px;
    width: 100%;
  }
  
  .carousel-card {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 300px !important;
    height: 100%;
    background-color: #fff;
  }
  
  .carousel-card > img {
    height: 100%;
    width: 300px;
  }

  .carousel-track {
    display: flex;
    position: absolute;
    left: 0;
    justify-content: center;
    align-items: center;
    height: 80px;
    gap: 16px;
    height: 100%;
    animation: slide 30s linear infinite;

  }
  .carousel-track_reverse {
    margin-left: 16px;
    display: flex;
    position: absolute;
    left: 0;
    justify-content: center;
    align-items: center;
    height: 80px;
    gap: 16px;
    height: 100%;
    animation: slide 30s linear infinite;

  } 

  @keyframes slide {
    0% {
      transform: translateX(-100%);
    }
    100% {
      transform: translateX(0);
    }
  }

  @keyframes slide_reverse {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(-100%);
    }
  }