div#Blogs {
    justify-content: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.window {
    width: 100%;
    min-height: 55vh;
    display: flex;
    background-color: white;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.toplay {
    background: black;
    position: relative;
    z-index: 887;
    flex-direction: row;
}


.window{
    overflow: hidden;
}
.window p {
    font-family: 'Poppins',sans-serif;
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 0.04em;
}
img.bg{
    position: absolute;
    left: 1%;
    top: 34%;
    rotate: 522deg;
}
img.bgs {
    position: absolute;
    right: 0%;
    z-index: 1;
    bottom: 50%;
}
.dropdown a:hover {
    color: black;
  }
  