@import url('https://fonts.googleapis.com/css2?family=Exo&family=Lustria&family=Rokkitt&family=Ubuntu:wght@500&display=swap');
.heading{
    color: white;
    font-size: 3rem;
    text-align: center;
    font-family: 'Exo', sans-serif;
    font-family: 'Lustria', serif;
    font-family: 'Rokkitt', serif;
    font-family: 'Ubuntu', sans-serif;
    margin: 50px 0;
}

.cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.card{
    width: 400px;
    border: 2px solid rgb(125, 125, 125);
    border-radius: 15px;
    margin: 30px;
    position: relative;
    height: 47rem;

}
.cardHeading{
    font-size: 30px;
    font-weight: 900;
    text-align: center;
    margin: 20px 0;
    color: #f7ae1b;
}
.logo{
    /* width: 270px;
    height: 270px;
    width: 175px;
    background-color: white; */
    margin: auto;
    border-radius: 15px;
}

.options{
    padding: 0 20px;
    text-align: center;
    list-style: none;
    color: white;
    margin: 30px;
    font-size: 18px;
}
.option{
    font-weight: 700;
    margin: 16px;
}
.optionWithSpan{
    margin-bottom: 0;
}
.optionSpan{
    font-size: 16px;
    color: rgb(232, 232, 232);
    margin: -5px 0;
    padding: 4px 0;
}
i{
    margin: 0 5px;
    color: white;
}
.btnContainer{
    display: flex;
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
}
.btn{
    background-color: #f7ae1b;
    color: black;
    font-weight: 900;
    border: none;
    padding: 16px 50px;
    cursor: pointer;
    border-radius: 30px;
    margin: 16px auto;
    font-size: 20px;
}
